@use '../../styles/variables.scss' as *;
@use '../../styles/mixins.scss' as *;

.product-card {
  .details {
    display: flex;
    img {
      width: 150px;
      height: 150px;
      border-radius: 5px;
      margin-right: 20px;
    }

    .title {
      margin-bottom: 5px;
    }
  }

  .qty {
    width: 150px;
    height: 40px;
    background: $color-base-background-1;
    color: $color-base-outline-button-labels;
    display: flex;
    font-size: 20px;
    outline: 1px solid $color-base-outline-button-labels;

    button {
      font-size: 22px;
      background: $color-base-background-1;
      color: $color-base-outline-button-labels;
      height: 100%;
      width: 50px;
      cursor: pointer;

      &:hover {
        outline: 4px solid $color-base-outline-button-labels;
      }
    }

    .value {
      flex: 1;
      text-align: center;
      line-height: 40px;
    }
  }
  
  .delete-icon {
    cursor: pointer;
    transition: color 0.3s ease;
    &:hover {
      color: crimson;
    }
  }
}
