@use './variables.scss' as *;
@use './utils.scss' as *;
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;0,700;1,100;1,200;1,300&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  scroll-behavior: smooth;
}

body {
  background: $bg-gradient-1 no-repeat;
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
}

.bg-main {
  background: $bg-gradient-1;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: underline;
  color: $color-base-text;

  &:hover {
    color: lighten($color: $color-base-text, $amount: 10%);
  }
}

hr {
  border-top: 0;
  border-bottom: 1px solid rgba($color-base-text, 0.08);
}

.form-control {
  width: 100%;
  display: block;
  background: $color-base-background-1;
  color: $color-base-text;
  font-size: 16px;
  padding: 15px 20px;
  outline: none;
  border: 1px solid $color-base-text;
  border-radius: 4px;

  &::placeholder {
    color: $color-base-text;
  }

  &:hover {
    outline: 1px solid $color-base-text;
  }

  &:focus {
    outline: 3px solid $color-base-text;

    &::placeholder {
      color: transparent;
    }
  }
}