@use './variables.scss' as *;
@use './mixins.scss' as *;

// padding and margins
@for $i from 0 through 10 {
  // padding
  .p-#{$i} {
    padding: 16px * $i !important;
  }
  .py-#{$i} {
    padding-top: 16px * $i !important;
    padding-bottom: 16px * $i !important;
  }
  .px-#{$i} {
    padding-inline-start: 16px * $i !important;
    padding-inline-end: 16px * $i !important;
  }
  .pt-#{$i} {
    padding-top: 16px * $i !important;
  }
  .pb-#{$i} {
    padding-bottom: 16px * $i !important;
  }
  .pl-#{$i} {
    padding-inline-start: 16px * $i !important;
  }
  .pr-#{$i} {
    padding-inline-end: 16px * $i !important;
  }

  // margins
  .m-#{$i} {
    margin: 16px * $i !important;
  }
  .my-#{$i} {
    margin-top: 16px * $i !important;
    margin-bottom: 16px * $i !important;
  }
  .mx-#{$i} {
    margin-inline-start: 16px * $i !important;
    margin-inline-end: 16px * $i !important;
  }
  .mt-#{$i} {
    margin-top: 16px * $i !important;
  }
  .mb-#{$i} {
    margin-bottom: 16px * $i !important;
  }
  .ml-#{$i} {
    margin-inline-start: 16px * $i !important;
  }
  .mr-#{$i} {
    margin-inline-end: 16px * $i !important;
  }
}

// paddings and margins on sm

@each $size in $screenSizes {
  @include screen-size($size) {
    @for $i from 0 through 10 {
      // padding
      .p-#{$size}-#{$i} {
        padding: 16px * $i !important;
      }
      .py-#{$size}-#{$i} {
        padding-top: 16px * $i !important;
        padding-bottom: 16px * $i !important;
      }
      .px-#{$size}-#{$i} {
        padding-inline-start: 16px * $i !important;
        padding-inline-end: 16px * $i !important;
      }
      .pt-#{$size}-#{$i} {
        padding-top: 16px * $i !important;
      }
      .pb-#{$size}-#{$i} {
        padding-bottom: 16px * $i !important;
      }
      .pl-#{$size}-#{$i} {
        padding-inline-start: 16px * $i !important;
      }
      .pr-#{$size}-#{$i} {
        padding-inline-end: 16px * $i !important;
      }

      // margins
      .m-#{$size}-#{$i} {
        margin: 16px * $i !important;
      }
      .my-#{$size}-#{$i} {
        margin-top: 16px * $i !important;
        margin-bottom: 16px * $i !important;
      }
      .mx-#{$size}-#{$i} {
        margin-inline-start: 16px * $i !important;
        margin-inline-end: 16px * $i !important;
      }
      .mt-#{$size}-#{$i} {
        margin-top: 16px * $i !important;
      }
      .mb-#{$size}-#{$i} {
        margin-bottom: 16px * $i !important;
      }
      .ml-#{$size}-#{$i} {
        margin-inline-start: 16px * $i !important;
      }
      .mr-#{$size}-#{$i} {
        margin-inline-end: 16px * $i !important;
      }
    }
  }
}

.w-100 {
  width: 100%;
}
.h-100 {
  height: 100%;
}
.d-flex {
  display: flex;
}
.d-block {
  display: block;
}
.flex-end {
  justify-content: flex-end;
}
.flex-start {
  justify-content: flex-start;
}
.js-center {
  justify-content: center;
}
.js-space-between {
  justify-content: space-between;
}
.al-items-center {
  align-items: center;
}

.bg-green {
  background-color: #10c45c;
}

.bg-crimson {
  background-color: crimson;
}

.bg-yellow {
  background-color: #eab830;
}

.bg-primary {
  background-color: #307ad5;
}

.bg-pink {
  background-color: #d94da6;
}

.text {
  &-center {
    text-align: center;
  }

  &-underline {
    text-decoration: underline;
  }

  &-bold {
    font-weight: bold;
  }
}

.fs-50 {
  font-size: 30px;
}

.overflow-x-auto {
  overflow-x: auto;
}
