@use '../../styles/variables.scss' as *;

.orders-page {
  color: $color-base-text;

  .order {
    margin-bottom: 15px;
    padding: 20px 0;
    border-bottom: 1px solid rgba($color-base-text, 0.08);

    .product {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    button {
    padding: 10px 20px;
    background: $color-base-background-1;
    color: $color-base-text;
    font-size: 16px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: 10px 0;

    &:hover {
      outline: 1px solid $color-base-text;
    }
  }
  }
}
