@use '../../styles/variables.scss' as *;
@use '../../styles/mixins.scss' as *;

.navbar {
  width: 100%;
  background: #0f3724;
  // background: $bg-gradient-1;
  // color: $color-base-text;
  color: #fff;
  border-bottom: 1px solid rgba($color-base-text, 0.08);
  position: relative;
  // position: sticky;
  // top: 0;
  // left: 0;
  // z-index: 99;
  p {
    text-align: center;
    padding: 8px 0;
  }

  &-content {
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .icon {
      font-size: 30px;

      &:hover {
        cursor: pointer;
        transform: scale(1.09);
      }
    }

    .left {
      display: flex;
      align-items: center;
      .logo {
        img {
          max-width: 90px;
        }
      }

      .menu-btn {
        display: block;
        @include lg {
          display: none;
        }
      }

      .collapse {
        position: absolute;
        top: 100%;
        left: -100%;
        transition: left 0.4s ease;
        width: 100%;
        z-index: 9;
        &.active {
          left: 0;
        }
        @include lg {
          display: block;
          position: static;
        }

        .links {
          width: 100%;
          background: $color-base-background-1;
          padding: 40px 0;
          @include lg {
            padding: 0;
          }

          li {
            width: 100%;
            padding: 5px 0;
            text-align: center;
            margin-bottom: 5px;
            cursor: pointer;
            a, &.link {
              // color: $color-base-text;
              color: #fff;
              &:hover {
                // font-weight: bold;
                color: lighten($color: $color-base-text, $amount: 10%);
                text-decoration: underline;
              }
            }
          }
          @include lg {
            display: flex;
            margin-left: 20px;
            background-color: transparent;
            li {
              margin-right: 10px;
              padding: 0;
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}
