@use '../../styles/variables.scss' as *;

.login {
  padding: 100px 0;
  .form {
    max-width: 500px;
    margin: auto;
    display: block;
  }

  button {
    padding: 20px 30px;
    background: $color-base-background-1;
    color: $color-base-text;
    font-size: 20px;
    border: none;
    outline: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
      outline: 1px solid $color-base-text;
    }
  }
}

