$breakpoints: (
  'xs': 0px,
  'sm': 576px,
  'md': 768px,
  'lg': 992px,
  'xl': 1200px,
  'xxl': 1400px,
);
$screenSizes: sm, md, lg, xl, xxl;

$box-shadow-color: rgb(0 0 0 / 5%);

$bg-gradient-1: linear-gradient(
  to bottom,
  rgba(17, 63, 41, 1),
  rgb(1, 26, 15)
);
$bg-gradient-2: linear-gradient(
  140deg,
  rgba(0, 0, 0, 1) 100%,
  rgba(48, 145, 211, 1) 100%,
  rgba(127, 72, 176, 1) 100%
);
$gradient-base-accent-1: linear-gradient(
  175deg,
  rgba(0, 0, 0, 1) 100%,
  rgba(33, 249, 150, 1) 100%
);
$gradient-base-accent-2: linear-gradient(
  228deg,
  rgba(0, 0, 0, 1) 100%,
  rgba(187, 48, 254, 1) 100%
);

// $color-base-text: rgb(63, 165, 117);
$color-base-text: #fff;
$color-shadow: rgb(63, 165, 117);
$color-base-background-1: rgb(18, 18, 18);
$color-base-background-2: rgb(0, 0, 0);
$color-base-solid-button-labels: rgb(18, 18, 18);
// $color-base-outline-button-labels: rgb(63, 165, 117);
$color-base-outline-button-labels: #fff;
$color-base-accent-1: rgb(18, 18, 18);
$color-base-accent-2: rgb(0, 0, 0);