@use '../../styles/variables.scss' as *;
@use '../../styles/mixins.scss' as *;

.product-page {
  color: $color-base-text;

  .image {
    img {
      max-height: 600px;
      object-fit: fill;
    }
  }

  .info {
    h1 {
      @include lg {
        font-size: 60px;
      }
    }
    .qty {
      width: 150px;
      height: 40px;
      background: $color-base-background-1;
      color: $color-base-outline-button-labels;
      display: flex;
      font-size: 20px;
      outline: 1px solid $color-base-outline-button-labels;
      
      button {
        font-size: 22px;
        background: $color-base-background-1;
        color: $color-base-outline-button-labels;
        height: 100%;
        width: 50px;
        cursor: pointer;

        &:hover {
          outline: 4px solid $color-base-outline-button-labels;
        }
      }

      .value {
        flex: 1;
        text-align: center;
        line-height: 40px;
      }
    }
  }
}

.cart-btn {
  width: 100%;
  background: $color-base-background-1;
  padding: 10px 0;
  text-align: center;
  margin-bottom: 20px;
  font-size: 17px;
  color: $color-base-outline-button-labels;
  cursor: pointer;
  transition: outline 0.2s ease;
  outline: 1px solid $color-base-outline-button-labels;

  &:hover {
    outline: 4px solid $color-base-outline-button-labels;
  }
}
