@use '../../styles/variables.scss' as *;
@use '../../styles/mixins.scss' as *;

.home {
  &-header {
    min-height: 115vh;
    background: #0b2217 url('../../assests/logo-navbar.png') no-repeat center;
    background-size: cover;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(#000, 0.3);
    }

    .content {
      width: 100%;
      position: absolute;
      top: 55%;

      color: #fff;
      text-align: center;

      .type-writer {
        margin-bottom: 20px;
        color: #ddd;
        font-size: 25px;
        font-weight: bold;
        @include lg {
          font-size: 70px;
        }
      }
      p {
        margin-bottom: 20px;
        font-size: 20px;
        color: #ddd;
      }
      a {
        padding: 12px 20px;
        background-color: transparent;
        border: 1px solid #ddd;
        color: #ddd;
        cursor: pointer;
        border-radius: 3px;
        font-size: 20px;
        display: inline-block;
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        text-decoration: none;

        &:hover {
          outline: 2px solid #ddd;
        }
      }
    }
  }

  .products {
    // background: $bg-gradient-1;
    color: $color-base-text;
    padding: 80px 0;

    .header {
      text-align: center;
      margin-bottom: 20px;

      .title {
        margin-bottom: 20px;
      }
      .subtitle {
        color: rgba($color: $color-base-text, $alpha: 0.75);
        font-size: 16px;
      }
    }

    .header-2 {
      font-size: 30px;
      margin-bottom: 20px;
    }

    .product {
      cursor: pointer;
      &:hover {
        img {
          transform: scale(1.3) rotate(-5deg);
        }
        .title {
          text-decoration: underline;
        }
      }
      .image {
        overflow: hidden;
        width: 100%;
        height: 250px;
        border-radius: 4px;
        img {
          transition: all 0.3s ease;
          width: 100%;
          height: 100%;
        }
      }

      .title {
        margin: 10px 0;
        color: rgba($color: $color-base-text, $alpha: 0.75);
      }

      .price {
        font-size: 18px;
      }
    }
  }
}
