@use '../../styles/variables.scss' as *;

.footer {
  padding: 20px 0;
  color: $color-base-text;
  border-top: 1px solid rgba($color-base-text, 0.08);
  border-bottom: 1px solid rgba($color-base-text, 0.08);

  .icons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;

    .icon {
      margin-right: 10px;
      font-size: 28px;
    }
  }
}

.my-info {
  padding: 20px 0;
  color: $color-base-text;
  text-align: center;
  word-wrap: break-word;
}
