@use '../../styles/variables.scss' as *;
@use '../../styles/mixins.scss' as *;

.cart {
  color: $color-base-text;
  .header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;

    h1 {
      font-weight: 300;
      @include lg {
        font-size: 60px;
      }
    }

    a {
      font-size: 22px;
    }
  }

  .header-2 {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba($color-base-text, 0.08);
  }

  .products {
    padding: 30px 0;
  }

  form {
    max-width: 500px;
    display: block;
    margin: auto;
  }
}
